@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: 0;
    list-style: none;
    text-decoration: none;
}

/* This is so we have access to all our CSS we use a lot and you want them to be uniformed  */
:root {
    --color-bg: #1f1f38;
    --color-bg-variant: #2C2C6C;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 2555, 0.6);

    --transition: all 400ms ease;
    
    /* Here we want the Width to change depending on the screen size */
    --container-width-lg: 75%;
    /* md means medium size */
    --container-width-md: 86%; 
     /* sm means small size */
    --container-width-sm: 90%; 
}

/*Here we want when we scroll it is smooth*/
html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(../src/assets/bg.png);
    
    
}


/* ----- HERE WILL BE THE GENERAL STYLES ------- */
.container {
    /* I Changed it from max to min */
    min-width: var(--container-width-lg); 
    margin: 0;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;

}

section > h2, section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition) ;
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    max-width: 100%;
    object-fit: cover;
}


/* ----- HERE WILL BE THE MEDIA QUERIED FOR MEDIUM SIZE DEVICES ----- */

@media only screen  and (min-width: 768px) and (max-width: 1024px) {
    .container {
    width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* ----- HERE WILL BE THE MEDIA QUERIED FOR SMALL SIZE DEVICES ----- */

@media only screen and (max-device-width: 640px) {
    .container {
    min-width: var(--container-width-sm);
    }

    section > h2{
        margin-bottom: 2rem;
    }
}

